.contact-wrapper {
    display: flex;
    flex-direction: column;
    max-width: 1024px;
    margin: 0 auto;
    padding: 24px;
    min-height: 100vh;
}

.contact-heading {
    font-size: 3rem;
    padding: 10px;
    margin-top: 100px;
    margin-bottom: 40px;
    color: var(--primary-color);
    text-align: center;
}

.contact-inner {
    display: flex;
    justify-content: center;
    /* align-items: center; */
    gap: 24px;
}

.contact-info {
    flex: 1;
    padding-top: 16px;
}

.contact-item {
    display: flex;
    align-items: center;
}

.contact-icon {
    font-size: 40px !important;
    color: #fff;
    background-color: var(--secondary-color);
    border-radius: 50%;
    margin: 8px;
    padding: 8px;
}

.contact-text {
    margin-left: 10px;
    color: var(--text-color);
    font-size: 18px;
}

.contact-item a:hover {
    color: var(--primary-color);
    cursor: pointer;
}

.contact-desc {
    padding: 10px;
    font-size: 1.25rem;
}

.contact-form {
    display: flex;
    flex-direction: column;
    flex: 1;
    /* padding: 60px 40px; */
}

.contact-form-message {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.form-item {
    font-size: 18px;
    margin: 14px 0;
}

.form-item:first-child {
    margin-top: 0;
}

.form-input,
.form-input textarea {
    border: 1px solid var(--light-gray);
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 8px;
}

.form-input:focus,
textarea:focus,
textarea:focus-visible {
    border: 1px solid var(--primary-color) !important;
    outline: none;
}

input,
textarea {
    font-size: 16px;
    font-family: 'Roboto', 'Helvetica Neue', sans-serif;
}

input:placeholder-shown,
textarea:placeholder-shown {
    font-size: 16px;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}

.btn.btn-send {
    font-weight: 400 !important;
    font-size: 14px !important;
    background-color: var(--secondary-color) !important;
    padding: 8px 14px;
    margin-top: 14px;
}

@media (max-width: 1024px) {
    .contact-wrapper {
        padding: 24px;
    }
}

@media (max-width: 480px) {
    .contact-inner {
        display: flex;
        flex-direction: column;
    }
    .contact-form {
        margin-top: 24px;
    }
    .contact-heading {
        margin-top: 0;
    }
}