    .about-wrapper {
        display: flex;
        flex: 1;
        width: 100%;
        max-width: 1024px;
        margin: 0 auto;
        padding: 24px;
        min-height: calc(100vh - 70px);
        flex-direction: row;
        justify-content: space-between;
    }
    
    .about-img {
        width: 30%;
        display: flex;
        position: relative;
    }
    
    .img-card {
        border-radius: 20px;
        padding: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        box-shadow: rgba(165, 163, 163, 0.8) 10px 10px 24px;
    }
    
    .img-card img {
        border-radius: 10px;
        object-fit: cover;
        z-index: 1;
    }
    
    .img-card:hover {
        transform: scale(1.05);
        transition: transform 0.2s ease-in-out 0s;
    }
    
    .about-content {
        display: flex;
        justify-content: center;
        flex-direction: column;
        /* margin-left: 100px; */
    }
    
    .about-heading {
        font-size: 3rem;
        padding: 10px;
        margin-bottom: 16px;
        color: var(--primary-color);
        text-align: center;
    }
    
    .about-desc {
        text-align: justify;
        font-size: 20px;
        font-weight: 300;
        line-height: 2rem;
        padding-left: 10px;
        margin-bottom: 20px;
    }
    
    .about-btns {
        padding: 10px;
    }
    
    .btn.btn-download {
        font-weight: 400 !important;
        font-size: 14px !important;
        background-color: var(--secondary-color) !important;
        padding: 8px 14px;
    }
    
    .btn:hover {
        opacity: 0.8;
        transition: opacity 0.2s ease-in-out 0s;
    }
    
    @media (max-width: 1024px) {
        .about-wrapper {
            padding: 24px;
        }
        .about-desc {
            font-size: 16px;
        }
    }