.header-wrapper {
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo:hover {
    cursor: pointer;
}

.logo__text {
    height: 40px;
    line-height: 43px;
    margin: 5px 0;
    padding-left: 24px;
    display: flex;
    font-size: 1.8rem;
    font-weight: 900;
    color: var(--primary-color);
    letter-spacing: 1px;
}

.logo__text span {
    color: var(--text-color);
}

nav {
    position: fixed;
    left: 0;
    top: 0;
    padding: 8px 0 10px;
    width: 100%;
    z-index: 2;
    background-color: transparent;
    transition: 0.3s ease-in-out;
}

nav.active {
    box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.157);
    background-color: #ffffff;
}

nav ul {
    display: flex;
}

nav ul li a {
    height: 40px;
    line-height: 1.8rem;
    margin: 6px 12px;
    padding: 6px 12px;
    display: inline-block;
    font-size: 1.2rem;
    font-weight: 500;
    color: var(--primary-color);
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
    position: relative;
    cursor: pointer;
}

nav ul li a:after {
    background: none repeat scroll 0 0 transparent;
    bottom: 0;
    content: "";
    display: block;
    height: 3px;
    right: 0;
    position: absolute;
    background: var(--primary-color);
    transition: width 0.3s ease 0s, left 0.3s ease 0s;
    width: 0;
}

nav ul li a:hover:after {
    width: 100%;
    left: 0;
    cursor: pointer;
}

nav ul li a nav .menu-btn,
nav ul li a .menu-icon {
    display: none;
}

@media (max-width: 480px) {
    .nav {
        padding: 8px 10px 10px;
    }
    .menu {
        display: none;
    }
}