.projects-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    max-width: 1024px;
    padding: 24px;
    margin: 0 auto;
    min-height: calc(100vh - 70px);
}

.projects-heading {
    font-size: 3rem;
    padding: 10px;
    margin-top: 100px;
    margin-bottom: 40px;
    color: var(--primary-color);
}

.projects-container {
    /* padding: 10px; */
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 24px;
}

@media (max-width: 480px) {
    .projects-wrapper {
        height: 100%;
    }
    .projects-heading {
        margin-top: 0;
    }
}