.project-card {
    display: flex;
    flex-direction: column;
    border-radius: 4px;
    width: calc(100% / 3 - 16px);
    overflow: hidden;
}

.project-preview {
    width: 100%;
    border-radius: 4px;
    overflow: hidden;
}

.project-preview img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform ease-in-out 0.5s;
}

.project-preview:hover img {
    transform: scale(1.05);
    cursor: pointer;
}

.project-info {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: column;
    flex: 1;
    padding: 16px 16px 0;
    background: linear-gradient(rgba(67, 67, 67, 0), rgba(67, 67, 67, 0.6), rgb(67, 67, 67, 1));
    transition: all ease-in-out 0.5s;
}

.project-card:hover .project-info {
    padding: 32px 16px 8px;
    background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.9), rgb(255, 255, 255, 1));
    height: max-content;
}

.project-title {
    font-size: 1.2rem;
    font-weight: 500;
    color: #fff;
    line-height: 24px;
    /* margin-bottom: 20px; */
}

.project-card:hover .project-title {
    color: #000;
}

.project-btns {
    padding-top: 8px;
}

.btn {
    padding: 4px 10px !important;
    border-radius: 2px !important;
}

.btn-filled {
    background-color: var(--secondary-color) !important;
}

.btn-outlined {
    color: var(--secondary-color) !important;
    border: 1.5px solid var(--secondary-color) !important;
}

@media (max-width: 768px) {
    .project-card {
        width: calc(100% / 2 - 12px);
    }
    .project-title {
        font-size: 16px;
    }
}

@media (max-width: 600px) {
    .project-card {
        width: calc(100%);
    }
    .project-title {
        font-size: 20px;
    }
}